.App-table-preload {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-table {
	height:1000px;
	display: flex;
	/*width:1250px;*/
}

.App-table--row .option-name--cell {
	width: 100%;
	margin-top: 8px;	

}
.App-table--row .option-name--cell .preview-icon {
	visibility: hidden;
	float: right;
	cursor: pointer;
	margin-right: 0.5em;	
}
.App-table--row:hover .preview-icon {
	/*visibility: visible;*/
	visibility: hidden;
}

